@import url(https://fonts.googleapis.com/css2?family=Muli:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.body {
  margin: 0;
}

.app-container {
  display: flex;
}

.text-editor-pane {
  display: flex;
}

.whiteboard-container {
  max-width: 45%;
  border: 3px solid black;
  padding: 0.5rem;
}

.whiteboard-top {
  display: flex;
  justify-content: space-between;
  background-color: hsl(255, 6%, 13%);

  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-radius: 0.5rem;
}

.editor-container {
  max-width: 50%;
  flex-grow: 1;
  flex-basis: 0;
  flex-direction: column;

  padding: 0.5rem;
  background-color: hsl(255, 6%, 25%);
}

.editor-top {
  display: flex;
  justify-content: space-between;
  background-color: hsl(255, 6%, 13%);
  color: white;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem;
}

.CodeMirror {
  height: 75vh !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  overflow: hidden;
}

.box {
  width: 500px;
  padding: 100px;
  top: 20%;
  right: 15%;
  position: absolute;
  background: #191919;
  text-align: center;
}

.box input[type="text"] {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #3498db;
  padding: 14px 10px;
  width: 200px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
}

.box input[type="text"]:focus {
  width: 280px;
  border-color: #2ecc71;
}

.box button[type="submit"] {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #2ecc71;
  padding: 14px 40px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
  cursor: pointer;
}

.box button[type="submit"]:hover {
  background: #2ecc71;
}

.box h1 {
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  font-weight: 500;
  font-family: sans-serif;
}
.box h3 {
  color: white;
  font-weight: 500;
  font-family: sans-serif;
}

body {
  background: #282b34;
}

.homepage {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  color: white;
  font-family: "Muli", sans-serif;

  width: 100%;
  height: 100vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #282b34;
}

.homepage {
  width: 500px;
  height: 500px;
  padding: 2rem;
  background-color: #2d343e;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
}
.homepage input {
  height: 50px;
  width: 80%;
  background-color: #404450;
  border: none;
  padding-left: 1rem;
  border-radius: 5px;
}
.homepage input:focus {
  outline: none;
}
.homepage button {
  font-size: 1rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
  width: 100px;
  border: none;
  background-color: #ffac41;
  border-radius: 5px;
  color: black;
}
.homepage button:hover {
  cursor: pointer;
}
.heading {
  font-size: 30px;
  font-weight: bold;
}
.para {
  text-align: center;
}

.outer input {
  height: 50px;
  width: 80%;
  background-color: #404450;
  border: none;
  color: white;
  padding-left: 1rem;
  font-size: medium;
  border-radius: 5px;
}

.outer input:focus {
  border: 1px solid blue;
  box-shadow: 0 0 10px #719ece;
}

